.testimonials {
  &__item {
    margin-bottom: 48px;

    &:last-child {
      margin-bottom: 0;
    }

    @include onDesktop {
      margin-bottom: 0;
    }
  }
}

.testimonial {
  &__photo {
    width: 100%;
    border-radius: 50%;
    grid-column: 2 / 4;
  }

  &__photo-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 24px;

    margin-bottom: 28px;

    transition: transform 0.7s;

    @include onDesktop {
      margin-bottom: 32px;
      transition: transform 0.7s;

      &:hover {
        transition: transform 0.7s;
        transform: scale(1.2);
      }
    }
  }

  &__name {
    margin: 0 0 4px;
    font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    text-align: center;
    color: $Color-blue;
  }

  &__role {
    margin: 0 0 4px;
    font-size: 14px;
    line-height: 27px;
    text-align: center;
    color: #9FADC2;
  }

  &__about {
    text-align: center;

  }
}

.link {
  text-decoration: none;
  color: $Color-blue;
}
